import React, { useEffect, useRef, useState } from 'react';
import commonFunc from 'untis/common-func';
import sty from "./fourthly.module.less";

const Fourthly = () => {
  const [isShow, setIsShow] = useState(false);
  const fDom: any = useRef(null);
  console.log("fDom", fDom); // current children offsetWidth

  useEffect(() => {
    DTP();

    // 监听滚动
    fDom.current.addEventListener("scroll", () => {
      if (fDom.current.scrollTop > fDom.current.scrollHeight - fDom.current.clientHeight - 200) {
        setIsShow(true);
      } else {
        setIsShow(false);
      }
      console.log("滚动了", fDom.current.scrollTop, fDom.current.scrollHeight);
    }, false);

    return () => {
      return fDom.current.removeEventListener("scroll", () => {}, false);
    }
  }, []);

  // div排列
  const DTP = () => {
    let aDiv = fDom.current.children;
    // box里面每个div的宽度
    let winW = aDiv[0].offsetWidth;
    // 列数
    let colums = parseInt((fDom.current.offsetWidth / winW).toString());
    console.log("列", colums, fDom.current.offsetWidth, winW);

    let colHeigths = []; // 保存每列高度总和
    for (let i=0; i<aDiv.length; i++) {
      if (i < colums) { // 第一行
        aDiv[i].style.position = "static";
        colHeigths.push(aDiv[i].offsetHeight);
      } else { // 从第二行开始
        let minHeight = colHeigths[0];
        let minIndex = 0;
        for (let j=0; j<colHeigths.length; j++) {
          if (minHeight > colHeigths[j]) {
            minHeight = colHeigths[j];
            minIndex = j;
          }
        }
        aDiv[i].style.position = "absolute";
        aDiv[i].style.left = aDiv[minIndex].offsetLeft + "px";
        aDiv[i].style.top = minHeight + "px";

        colHeigths[minIndex] += aDiv[i].offsetHeight;
      }
    }
  };

  const scrollAction = () => {
    fDom.current.scrollTop = "0px";
    setIsShow(false);
  };

  return (
    <div className={sty.main}>
      <div className={sty.box} ref={fDom}>
        {
          Array(7).fill(1).map((item: any, index: number) => {
            return <div key={index} style={{ background: commonFunc.rgbaAction(), height: `${Math.random() * 5}rem` }}>{index}</div>
          })
        }
      </div>
      {
        isShow && <div className={sty.btn} onClick={scrollAction}>回到顶部</div>
      }
    </div>
  );
};

export default Fourthly;
