import React, { useEffect, useRef, useState } from 'react';
import sty from "./sixth.module.less";
let FL: any; // 左侧菜单宽度
let FT: any; // 头部高度
let lf: any;
let tp: any;

const Sixth = () => {
  const [left, setLeft] = useState();
  const [top, setTop] = useState();
  const dom: any = useRef(null);

  useEffect(() => {
    FL = dom.current.offsetParent.offsetLeft;
    FT = dom.current.offsetParent.offsetTop;
  }, []);

  const downAction = () => {
    FL = dom.current.offsetParent.offsetLeft;
    FT = dom.current.offsetParent.offsetTop;
  };

  const moveAction = (e: any) => {
    lf = e.pageX - dom.current.offsetLeft - FL + "px";
    tp = e.pageY - dom.current.offsetTop - FT + "px";
  };

  const upAction = () => {
    setLeft(lf);
    setTop(tp);
  };

  return (
    <div
      ref={dom}
      className={sty.main}
      onMouseDown={downAction}
      onMouseMove={moveAction}
      onMouseUp={upAction}
    >
      <div className={sty.box} style={{ left, top }}>123</div>
    </div>
  );
};

export default Sixth;
