import React, { useEffect, useState } from 'react';
import sty from "./secondComp.module.less";

const SecondComp = () => {
  const [eq, setEq] = useState(1);
  useEffect(() => {
  }, []);

  const leftAction = () => {
    let ele: any = document.getElementsByClassName(sty.imgs)[0];
    if (eq === 1) {
      setEq(3);
      ele.style.left = `0px`;
    } else {
      setEq(eq - 1);
      ele.style.left = `-${ele.offsetWidth * (eq - 1)}px`;
    }
  }

  const rightAction = () => {
    let ele: any = document.getElementsByClassName(sty.imgs)[0];
    console.log("x", ele, ele.scrollWidth, ele.offsetWidth);
    if (eq === 3) {
      setEq(1);
      ele.style.left = `0px`;
    } else {
      setEq(eq + 1);
      ele.style.left = `-${ele.offsetWidth * eq}px`;
    }
  }

  return (
    <div className={sty.main}>
      <div className={sty.left} onClick={leftAction}><i className="iconfont paicon-slide_left" /></div>
      <div className={sty.right} onClick={rightAction}><i className="iconfont paicon-slide_right" /></div>
      <div className={sty.dots}><div /><div /><div /></div>
      <div className={sty.imgs}>
        <div>1</div>
        <div>2</div>
        <div>3</div>
      </div>
    </div>
  );
};

export default SecondComp;
