import React, { useState, useEffect, memo } from 'react';
import { withRouter } from 'react-router-dom';
import { useStore } from 'store/store';
import munuList from '../../router/routerConfig';
import './menu.less';

const Menus = (props: any) => {

  const [menuUrl, setMenuUrl] = useState<any[]>([]);
  const [selectFater, setSelectFater] = useState("");

  useEffect(() => {
    init(props.history.location.pathname);
  }, [])

  const init = (path: any) => {
    munuList[0].subMenu.forEach((item: any) => {
      if (item.subPath === path) {
        setSelectFater(munuList[0].path);
        setMenuUrl([munuList[0].path]);
      }
    })
  }

  const setShowAction = (path: string) => {
    const newMenu = [...menuUrl];
    const tpsIq = menuUrl.findIndex(item => item === path);
    if (tpsIq === -1) {
      newMenu.push(path);
    } else {
      newMenu.splice(tpsIq, 1);
    }
    setMenuUrl(newMenu);
  }

  const toPageAction = (path: string, item: any) => {
    setSelectFater(item.path);
    props.history.push(path);
  }

  const fanc = () => {
    const pathname = props.history.location.pathname.replace(/\/$/g, "");
    const i = pathname.lastIndexOf("/");
    const actPar = pathname.slice(0, i);
    return [actPar, pathname];
  }

  return (
    <div className="example">
      <ul className="menu">
        {
          munuList.map((item: any, index: number) => {
            const fd = menuUrl.find(one => one === item.path);
            const height = fd ? `${item.subMenu.length * 0.5}rem` : 0;
            const isSelect = item.path === selectFater;
            return <li key={index} className="menu-item">
              <div className={`item-title ${isSelect ? "selected" : ""}`} onClick={() => setShowAction(item.path)}>
                <i className="iconfont paicon-building"></i>
                <span>{item.name}</span>
              </div>
              <ul className="item-child" style={{ height, transition: "height 0.3rem linear", overflow: "hidden" }}>
                {
                  item.subMenu.map((subItem: any) => {
                    const subActive = fanc()[1] === subItem.subPath;
                    return <li onClick={() => toPageAction(subItem.subPath, item)} key={subItem.subPath} className={`child-one ${subActive ? "subSelected" : ""}`}>{subItem.subName}</li>;
                  })
                }
              </ul>
            </li>;
          })
        }
      </ul>
    </div>
  );
};

export default memo(withRouter(Menus));