import React, { useEffect } from 'react';
import "./sampleCanvas.less";

const SampleCanvas = () => {

	useEffect(() => {
		const canvas: any = document.getElementById("canvas");
		const context = canvas && canvas.getContext("2d");
		const WIDTH = document.documentElement.clientWidth;
		const HEIGHT = document.documentElement.clientHeight;
		const initRoundPopulation = 80;
		for (let i = 0; i < initRoundPopulation; i++) {
			const obj = Round_item(i, Math.random() * WIDTH, Math.random() * HEIGHT);
			draw(context, obj);
		}
	}, []);

	const Round_item = (index: number, x: any, y: any) => {
		const rand = (Math.floor(Math.random() * 10) + 1) / 10 / 2;
		const obj = {
			index,
			x,
			y,
			r: Math.random() * 2 + 1,
			alpha: rand,
			color: "rgba(0,0,0," + rand + ")",
		};
		return obj;
	}

	const draw = (context: any, obj: any) => {
		context.fillStyle = obj.color;
		context.shadowBlur = obj.r * 2;
		context.beginPath();
		context.arc(obj.x, obj.y, obj.r, 0, 2 * Math.PI, false);
		context.closePath();
		context.fill();
	}

	return (
		<div className="sample-canvas-contain">
			<canvas id="canvas"></canvas>
		</div>
	);
};

export default SampleCanvas;