import React, { useState } from 'react';
import { Button } from "antd";
import sty from "./thirdly.module.less";
let timer: any;
let i = 0;

const Thirdly = () => {
  const [time, setTime] = useState<any>("00时00分00秒");
  const [pause, setPause] = useState(true);

  const setTim = () => {
    timer = setInterval(() => {
      i++;
      let hour = parseInt(String(i/3600));
      let min = parseInt(String(i/60))%60;
      let second = i%60;
      setTime(`${hour >= 10 ? hour : "0" + hour}时${min >= 10 ? min : "0" + min}分${second >= 10 ? second : "0" + second}秒`);
      console.log(i);
    }, 1000);
  };

  const startAction = () => {
    console.log("timer", timer);
    if (timer) {
      setPause(true);
      clearInterval(timer);
      timer = null;
    } else {
      setPause(false);
      setTim();
    }
  };

  const resertAction = () => {
    clearInterval(timer);
    setPause(true);
    setTime("00时00分00秒");
    i = 0;
  };

  return (
    <div className={sty.main}>
      <div className={sty.timer}>
        <div>{time}</div>
        <Button onClick={startAction}>{`${pause ? "开始" : "暂停"}计时`}</Button>
        <Button onClick={resertAction}>重置</Button>
      </div>
    </div>
  );
};

export default Thirdly;
