import React, { useState, useEffect } from 'react';
import E from 'wangeditor';
import sty from "./quill.module.less";

let editor: any;

const Quill = () => {
	const [content, setContent] = useState('')

	useEffect(() => {
		editor = new E("#div1");
		// 配置(图片上传)
		// editor.config = {
		// 	uploadImgServer: 'http://172.22.51.149:8080/cpmApi//project/file/upload',
		// 	uploadImgMaxSize: 50 * 1024 * 1024, // 2M
		// 	uploadImgMaxLength: 5, // 一次最多上传 5 个图片
		// 	uploadImgParams: {
		// 		token: 'xxxxx'
		// 	}
		// };
		editor.config.onchange = () => (newHtml: any) => {
			setContent(newHtml);
		};
		/**一定要创建 */
		editor.create()
		return () => {
			// 组件销毁时销毁编辑器  注：class写法需要在componentWillUnmount中调用
			editor.destroy()
		}
	}, [])

	// 获取html方法1
	const getHtml = () => {
		alert(content)
	}

	// 获取html方法2
	const getHtml1 = () => {
		alert(editor.txt.html())
	}

	// 获取text
	const getText = () => {
		alert(editor.txt.text())
	}

	return (
		<div className={sty.quill}>
			<div id="div1"></div>
			<button onClick={getHtml}>获取html</button>
			<button onClick={getHtml1}>获取html1</button>
			<button onClick={getText}>获取text</button>
		</div>
	);
};

export default Quill;
