import React, { useEffect } from "react";
// @ts-ignore
import videojs from "video.js/dist/video.min";
// @ts-ignore
import videoZhCN from "video.js/dist/lang/zh-CN.json";
import "video.js/dist/video-js.min.css";
const myWindow: any = window;
videojs.addLanguage("zh-CN", videoZhCN);
import "./videoPlay.less";

const Controls = {
  play: "playToggle",
  volume: "volumePanel",
  seekbar: "progressControl",
  timer: "remainingTimeDisplay",
  playbackrates: "playbackRateMenuButton",
  fullscreen: "fullscreenToggle",
};

const defaultProps = {
  src: "",
  poster: "",
  controls: true,
  controlBar: {
    pictureInPictureToggle: true
  },
  autoplay: false,
  preload: "auto",
  playbackRates: [0.5, 1, 1.5, 2],
  hidePlaybackRates: true,
  className: "",
  hideControls: [],
  bigPlayButton: true,
  bigPlayButtonCentered: true,
  muted: true,
  onReady: () => {
    return "";
  },
  onPlay: () => {
    return "";
  },
  onPause: () => {
    return "";
  },
  onTimeUpdate: () => {
    return "";
  },
  onSeeking: () => {
    return "";
  },
  onSeeked: () => {
    return "";
  },
  onEnd: () => {
    return "";
  }
};
function VideoPlayer(props: any) {
  const newProps: any = {
    ...JSON.parse(JSON.stringify(defaultProps)),
    ...props
  };
  const playerId: any = props.playerId || `videoplayer${Number(Date.now()) * 1}`;
  let player: any;

  useEffect(() => {
    // init_player(newProps);
    init_player_events(newProps);
    return () => {
      if (player) {
        player.dispose();
      }
    };
  }, []);

  useEffect(
    () => {
      init_player(newProps);
    },
    [props.src]
  );

  // tslint:disable-next-line:variable-name
  const init_player = (option: any) => {
    const playerOptions: any = generate_player_options(option);
    // @ts-ignore
    player = videojs(document.getElementById(`${playerId}`), playerOptions);
    player.src(option.src);
    player.poster(option.poster);
  };

  // tslint:disable-next-line:variable-name
  const generate_player_options = (v: any) => {
    const playerOptions = {
      controls: v.controls,
      autoplay: v.autoplay,
      preload: v.preload,
      width: v.width,
      height: v.height,
      bigPlayButton: v.bigPlayButton,
      playbackRates: [0.5, 1, 1.5, 2],
      language: "zh-CN",
      controlBar: ["exitPictureInPicture"]
    };
    const hidePlaybackRates = v.hidePlaybackRates;
    if (!hidePlaybackRates) {
      playerOptions.playbackRates = v.playbackRates;
    }
    return playerOptions;
  };
  /* eslint-enable */
  // tslint:disable-next-line:variable-name
  const init_player_events = (pro: any) => {
    let currentTime = 0;
    let previousTime = 0;
    let position = 0;

    if (!player) return;

    player.ready(() => {
      // pro.onReady(player);
      myWindow.player = player;
    });
    player.on("play", () => {
      // pro.onPlay(player.currentTime());
      player.currentTime();
    });
    player.on("pause", () => {
      player.currentTime();
      // pro.onPause(player.currentTime());
    });
    player.on("timeupdate", () => {
      // pro.onTimeUpdate(player.currentTime());
      player.currentTime();
      previousTime = currentTime;
      currentTime = player.currentTime();
      if (previousTime < currentTime) {
        position = previousTime;
        previousTime = currentTime;
      }
    });
    player.on("seeking", () => {
      // player.off("timeupdate", () => {
      //   console.log("")
      // });
      // player.one("seeked", () => {
      //   console.log("")
      // });
      player.currentTime();
      // pro.onSeeking(player.currentTime());
    });

    player.on("seeked", () => {
      const completeTime = Math.floor(player.currentTime());
      console.log(completeTime, position);

      // pro.onSeeked(position, completeTime);
    });
    player.on("ended", () => {
      // pro.onEnd();
    });
  };

  return (
    <div data-vjs-player={true} style={{ width: "100%!important", height: "100%!important" }}>
      <video
        id={playerId}
        className={`video-js vjs-big-play-centered ${props.className}`}
        style={{ width: "100%!important", height: "100%!important" }}
      />
    </div>
  );
}

export default VideoPlayer;
