import React, { useState } from 'react';
import sty from "./fifth.module.less";
let timer: any;
let d = 0; // 角度
let r = 1; // 半径

const Fifth = () => {
  const [left, setLeft] = useState("1.95rem");
  const [top, setTop] = useState("0.95rem");
  const [isStop, setIsStop] = useState(true);

  const startAction = () => {
    if (timer) {
      clearInterval(timer);
      timer = null;
      setIsStop(true);
    } else {
      setIsStop(false);
      timer = setInterval(() => {
        let lf = 0.95 + r*Math.cos(d) + "rem";
        let tp = 0.95 + r*Math.sin(d) + "rem";
        setLeft(lf)
        setTop(tp);
        d += 0.05;
      }, 30);
    }
  }

  return (
    <div className={sty.main}>
      <div className={sty.btn} onClick={startAction}>{isStop ? "开始运动" : "暂停运动"}</div>
      <div className={sty.box}>
        <div style={{ left, top }} />
      </div>
    </div>
  );
};

export default Fifth;
