import React, { useEffect, useState } from 'react';
import calendar from "untis/calendar";
import sty from "./calendarComp.module.less";
import moment from "moment";
const datefarmat = "HH:mm:ss";
let timer: any;

// 获取某月的天数：daysInMonth()
// 获取某月的第一天：startOf(‘month’)
// 获取某天是星期几：weekday()
// 增加时间：add(Number, String)
// 减去时间：subtract(Number, String)
// 设置中文的语言环境：locale(‘zh-ch’)
// 获取星期数据：weekdaysMin()

const CalendarComp = () => {
	const [curTime, setCurTime] = useState<string>(moment().format(datefarmat));
	const [showMonth, setShowMonth] = useState<any>(moment());
	const weekArr = moment.weekdaysMin(true); // 根据语言环境获取不同的星期数组

	useEffect(() => {
		refreshTime();
		() => {
			return clearInterval(timer);
		}
	}, []);

	useEffect(() => {
		setDate(showMonth);
	}, [showMonth]);

	const refreshTime = () => {
		timer = setInterval(() => {
			setCurTime(moment().format(datefarmat))
		}, 1000)
	}

	//某月的天数
	const getMonthDays = (momentObj: any) => {
    return momentObj.daysInMonth();
	}

	//选中月份的第一天是星期几
	const getWeekDays = (momentObj: any) => {
		return momentObj.startOf('month').weekday();
	}

	//获取农历
	const getDayCn = (year: any, month: any, date: any) => {
		// 把calendar文件中的calendar对象挂在了window对象上，通过属性调用calendar
		// 这样不会更改源文件
    let dayCn: any = calendar.solar2lunar(year, month, date);
    let result = "";
    if (dayCn.Term) { //如果有节气的话，换成节气
        result = dayCn.Term;
    } else if (dayCn.festival) { //如果有节日的话，换成节日
        result = dayCn.festival;
    } else if (dayCn.lunarFestival) { //如果有中国传统的节日的话，换成传统节日（春节、元宵节、端午节）
        result = dayCn.lunarFestival;
    } else {
        result = dayCn.IDayCn; //都没有的话就是农历
    }
    return result;
	}

	//生成日历
	const setDate = (m: any) => {
		const node = document.querySelector(".time");
		if (!node) return;

		// 获得当前月的天数  和 第一天的星期数
    let curDays = getMonthDays(m); // 当前天数
    let curWeek = getWeekDays(m.clone()); // 当前月第一天的星期(索引值)
    let upDays = getMonthDays(m.clone().subtract(1, 'month')); // 上月的天数

    let strDate = ''; //生成的结构
		let nextFirstDate = 0; //下个月的起始日期
		for (let i = 0; i < 42; i++) {
			if (i < curWeek) {
				strDate = `
				<div class=${sty.speical}>
						<div>${upDays}</div>
						<div>${getDayCn(m.year(),m.month(),upDays)}</div>
				</div>${strDate}`;
				upDays--
			} else if (i >= curDays + curWeek) { //去除掉当月天数+上月天数就是下月天数)
				nextFirstDate++;
				strDate += `
				<div class=${sty.speical}>
						<div>${nextFirstDate}</div>
						<div>${getDayCn(m.year(),m.month()+2,nextFirstDate)}</div>
				</div>`;
			} else {
				let currentClass = m.date() == i - curWeek + 1 ? 'current' : '';
				if (m.year() != moment().year() || m.month() != moment().month()) {
						currentClass = '';
				}
				strDate += `
				<div class = ${currentClass}>
						<div>${i-curWeek+1}</div>
						<div>${getDayCn(m.year(),m.month()+1,i-curWeek+1)}</div>
				</div>`;
			}
		}
		node.innerHTML = strDate; // 渲染日历
	}

	const upMonth = () => {
		setShowMonth(moment(showMonth).subtract(1, 'month'));
	}

	const downMonth = () => {
		setShowMonth(moment(showMonth).add(1, 'month'));
	}

	const showCn = (m: any) => {
		console.log(m.year(), m.month()+1, m.date());
    let dayCn: any = calendar.solar2lunar(m.year(), m.month()+1, m.date());
    return dayCn.IMonthCn + dayCn.IDayCn;
	}

	return (
		<div className={sty.main}>
			<div className={sty.tit}>
				<div>{curTime}</div>
				<div><span>{moment().format("YYYY年MM月DD日")}</span><span>{showCn(moment())}</span></div>
			</div>
			<div className={sty.head}>
				<div>{moment(showMonth).format("YYYY年MM月")}</div>
				<div>
					<div onClick={upMonth}>∨</div>
					<div onClick={downMonth}>∧</div>
				</div>
			</div>
			<div className={sty.list}>
				<div className={sty.week}>
					{
						weekArr.map((item: any) => {
							return <div>{item}</div>;
						})
					}
				</div>
				<div className={`${sty.time} time`} />
			</div>
		</div>
	);
};

export default CalendarComp;