import React, { useEffect, useState } from 'react';
import FirstComp from "./components/firstComp/firstComp";
import SecondComp from "./components/secondComp/secondComp";
import Thirdly from "./components/thirdly/thirdly";
import Fourthly from "./components/fourthly/fourthly";
import Fifth from "./components/fifth/fifth";
import Sixth from "./components/sixth/sixth";
import Seventh from "./components/seventh/seventh";
import Eighth from "./components/eighth/eighth";
import sty from "./jsCommon.module.less";

const modeList = [
  { order: 0, mode: FirstComp },
  { order: 1, mode: SecondComp },
  { order: 2, mode: Thirdly },
  { order: 3, mode: Fourthly },
  { order: 4, mode: Fifth },
  { order: 5, mode: Sixth },
  { order: 6, mode: Seventh },
  { order: 7, mode: Eighth },
  { order: 8, mode: FirstComp },
  { order: 9, mode: FirstComp },
  { order: 10, mode: FirstComp }
];

const JsCommon = () => {

  useEffect(() => {
  })

  return (
    <div className={sty.contain}>
      {
        modeList.map((item: any) => {
          return <div key={item.order} className={sty.box}>
            <item.mode />
          </div>;
        })
      }
    </div>
  );
};

export default JsCommon;
