import React, { useEffect, useState } from 'react';
import sty from "./firstComp.module.less";

const FirstComp = () => {
  const [mins, setMins] = useState<any[]>([]);

  useEffect(() => {
    let mins = Array(360 / 6).fill(1).map((i: any, index: number) => (index * 6));
    setMins(mins);
    setDate();
    const timer = setInterval(setDate, 1000);
    return () => {
      timer && clearInterval(timer);
    }
  }, []);

  const setDate = () => {
    let now = new Date();
    let hourDiv: any = document.querySelector(".hourHand");
    let minDiv: any = document.querySelector(".minHand");
    let secondDiv: any = document.querySelector(".secondHand");
    let iH = now.getHours();
    let iM = now.getMinutes();
    let iS = now.getSeconds();
    hourDiv.style.transform = `rotate(${iH * 30 + iM / 2}deg)`;
    minDiv.style.transform = `rotate(${iM * 6}deg)`;
    secondDiv.style.transform = `rotate(${iS * 6}deg)`;
  }

  return (
    <div className={sty.main}>
      <div className={sty.clockFace}>
        <div className={sty.scale}>
          {
            mins.map((degress: any, index: number) => {
              return <div
                  key={index}
                  style={{ transform: `rotate(${degress}deg)`, height: index % 5 === 0 ? "0.1rem" : "0.06rem" }}
                />;
            })
          }
        </div>
        <div className={`${sty.hand} hourHand`}></div>
        <div className={`${sty.hand} minHand`}></div>
        <div className={`${sty.hand} secondHand`}></div>
        <div className={sty.bail}></div>
      </div>
    </div>
  );
};

export default FirstComp;