import React, { useEffect, useState } from 'react';
import One from "./one/one";
import Two from "./two/two";
import "./simplePage.less";

const modeList = [
  { name: "第一个页面", order: "0", mode: One },
  { name: "第二个页面", order: "1", mode: Two },
];

const modeComp: any = {
  "0": <One />,
  "1": <Two />,
};

const SimplePage: React.FC<any> = () => {
  const [order, setOrder] = useState<any>("1");

  return (
    <div className="page-base-js">
      <div>
        {
          modeList.map((item: any, index: number) => {
            return <div key={index} onClick={() => setOrder(String(index))} className={order === String(index) ? "select" : ""}>
              {item.name}
            </div>;
          })
        }
      </div>
      <div>
        {modeComp[order]}
      </div>
    </div>
  );
};

export default SimplePage;