import React, { useEffect, useRef, useState } from 'react';
import sty from "./seventh.module.less";
const LIST = [
  { name: "服饰", bg: "rgb(202, 151, 135)" },
  { name: "家电", bg: "rgb(202, 197, 135)" },
  { name: "数码", bg: "rgb(151, 207, 134)" },
  { name: "美妆", bg: "rgb(134, 207, 183)" },
  { name: "母婴", bg: "rgb(134, 172, 207)" },
  { name: "食品", bg: "rgb(166, 134, 207)" }
];

const Seventh = () => {
  const dom: any = useRef(null);

  useEffect(() => {

  }, []);

  return (
    <div
      ref={dom}
      className={sty.main}
    >
      <div className={sty.left}>
        {
          LIST.map((item: any, index: number) => {
            return <div key={index} style={{ background: item.bg }}>{`${index + 1}楼`}</div>;
          })
        }
      </div>
      <div className={sty.right}>
        {
          LIST.map((item: any, index: number) => {
            return <div key={index} style={{ background: item.bg, height: `${Math.random() * 5}rem` }}>
              {`${index + 1}楼${item.name}区`}
            </div>;
          })
        }
      </div>
    </div>
  );
};

export default Seventh;
