import React, { useEffect, useState } from "react";
import { Popover } from "antd";
import moment from "moment";
import sty from "./one.module.less";
const FORMAT = "YYYY-MM-DD";
const weeks = ["日", "一", "二", "三", "四", "五", "六"];

const One: React.FC<any> = () => {
  const [startTime, setStartTime] = useState<any>("2022-04-05");
  const [endTime, setEndTime] = useState<any>("2022-06-14");
  const [daysList, setDaysList] = useState<any[]>([]);
  const [dataSours, setDataSours] = useState<any[]>([]);
  const [daysTotal, setDaysTotal] = useState<any[]>([]);
  console.log(setStartTime, setEndTime);

  useEffect(() => {
    Dates(startTime, endTime);
    getList();
  }, []);

  useEffect(() => {
    if (!daysList.length) return;
    const arr = [...dataSours];
    arr.forEach((item: any) => {
      item.list = daysList.map((one: any) => {
        const fd = item.list.find((two: any) => two.time === one);
        if (fd) {
          return fd;
        } else {
          return {time: one, work: 0};
        }
      });
    });
    getDaysNum(arr);
    setDataSours(arr);
  }, [daysList]);

  const getDaysNum = (arr: any[]) => {
    const arr2 = JSON.parse(JSON.stringify(arr));
    const list = arr2.reduce((array: any[], item: any) => {
      return array.concat(item.list);
    }, []);
    const list2: any[] = [];
    list.forEach((item: any) => {
      const fd = list2.find((one: any) => one.time === item.time);
      if (fd) {
        fd.work = fd.work + item.work;
      } else {
        list2.push(item);
      }
    }, []);
    setDaysTotal(list2);
  };

  const Dates = (startDate: string, endDate: string) => {
    const list: any[] = [];
    const start = moment(startDate);
    const end = moment(endDate);
    const day = end.diff(start, "days");
    list.push(start.format(FORMAT));
    for (let i = 1; i <= day; i++) {
      list.push(start.add(1, "days").format(FORMAT));
    }
    setDaysList(list);
  };

  const getList = () => {
    const list: any[] = [];
    for(let i=0; i<20; i++) {
      const obj = {
        name: "张川",
        num: "98",
        list: [
          { time: "2022-06-01", work: 1.4 },
          { time: "2022-06-03", work: 2.4 },
          { time: "2022-06-05", work: 3.4 },
          { time: "2022-06-08", work: 4.4 },
          { time: "2022-06-11", work: 5.4 },
        ]
      };
      list.push(obj);
    }
    setDataSours(list);
  };

  const content = (
    <div>
      <p>Content</p>
      <p>Content</p>
    </div>
  );

  const addClass = (time: string) => {
    const bg = ["日", "六"].indexOf(weeks[moment(time).day()]) !== -1 ? sty.weekBg : "";
    const line = weeks[moment(time).day()] === "日" ? sty.week : "";
    return [bg, line];
  };

  const showWeek = (time: string) => {
    const start = moment(time).subtract(6, "days").format(FORMAT);
    const weekNum = daysTotal.reduce((num: number, one: any) => {
      if (moment(one.time).isBetween(start, time, "days", "[]")) {
        num += one.work;
      }
      return num;
    }, 0);
    return Math.round(weekNum);
  };

  return <div className={sty.content}>
    <div className={sty.main}>
      <div className={sty.tHead}>
        <div>
          <div className={sty.name} />
          <div className={sty.tol} />
          {
            daysList.map((item: any, index: number) => {
              return <div key={index} className={`${sty.td} ${addClass(item)[1]}`}>{moment(item).format("DD")}</div>;
            })
          }
        </div>
        <div>
          <div className={sty.name}>名称</div>
          <div className={sty.tol}>E</div>
          {
            daysList.map((item: any, index: number) => {
              return <div key={index} className={`${sty.td} ${addClass(item)[1]}`}>{weeks[moment(item).day()]}</div>;
            })
          }
        </div>
      </div>
      <div className={sty.tBody}>
        {
          dataSours.map((item: any, index: number) => {
            return <div key={index}>
              <div className={sty.name}>{dataSours[index].name}</div>
              <div className={sty.tol}>{dataSours[index].num}</div>
              {
                item.list.map((one: any, eq: number) => {
                  return  one.work ? <Popover
                    content={content}
                    title="Title"
                    trigger="click"
                    getPopupContainer={(e: any) => e}
                  >
                    <div key={eq} className={`${sty.td} ${sty.select} ${addClass(one.time)[0]} ${addClass(one.time)[1]}`}>{one.work ? one.work : null}</div>
                  </Popover> : <div key={eq} className={`${sty.td} ${addClass(one.time)[0]} ${addClass(one.time)[1]}`} />;
                })
              }
            </div>;
          })
        }
      </div>
      <div className={sty.total}>
        <div className={sty.name} />
        <div className={sty.tol}><span>每天工时合计:</span></div>
        {
          daysTotal.map((item: any, index: number) => {
            return <div key={index} className={`${sty.td} ${addClass(item.time)[1]}`}>
              {Math.round(item.work)}
            </div>;
          })
        }
      </div>
      <div className={sty.total}>
        <div className={sty.name} />
        <div className={sty.tol}><span>每周工时合计:</span></div>
        {
          daysTotal.map((item: any, index: number) => {
            return <div key={index} className={`${sty.td} ${addClass(item.time)[1]}`}>
              {weeks[moment(item.time).day()] === "日" ? showWeek(item.time) : ""}
            </div>;
          })
        }
      </div>
    </div>
  </div>;
};

export default One;
