import React, { useEffect, useState } from 'react';
import Sty from "./drawEvent.module.less";
import ClipboardJS from "clipboard";
import TextLoop from "react-text-loop";
import { message } from 'antd';

const DrawEvent = () => {
	const [clipboard, setClipboard] = useState<any>();
	const list = [
		{ name: "列表one", code: "sfsfwefegergeesdfajflsjfiwefsjflskjflskjfaekjn" },
		{ name: "列表two", code: "sldfosdfjsoifjwjifsjenwrjiejjsoijdfsjfoisajfjfs" },
		{ name: "列表three", code: "sdfkwejnwogoajsdjfosjofjiasjdfjsjfdasfkefenaeff" }
	];

	useEffect(() => {
		const el = document.querySelectorAll(".copy-btn");
		const clipboardArr: any = [];
		el.forEach((item: any) => {
			const cbd = new ClipboardJS(item);
			cbd.on('success', function (e: any) {
				message.success("复制成功!");
			});
			cbd.on('error', function (e: any) {
				message.success("复制失败!");
			});
			clipboardArr.push(cbd);
		});
		setClipboard(clipboardArr);

		return () => {
			clipboard && clipboard.forEach((item: any) => {
				item.destory();
			});
		};
	}, []);

	return (
		<div className={Sty.drawEventContain}>
			<div className={Sty.copy}>
				{
					list.map((item: any, i: number) => {
						return <div key={i} className={Sty.oneLine}>
							<div>{item.name}</div>
							<div
								className="copy-btn"
								data-clipboard-text={item.code}
							>
								复制
							</div>
						</div>;
					})
				}
			</div>
			<div className={Sty.loop}>
				<TextLoop>
					<span>Trade faster</span>
					<span>Increase sales</span>
					<span>Stock winners</span>
				</TextLoop>
			</div>
		</div>
	);
};

export default DrawEvent;