import React, { useEffect, useState } from 'react';
import LineWidth from "./components/lineWidth/lineWidth";
import MouseEvent from "./components/mouseEvent/mouseEvent";
import DrawEvent from "./components/drawEvent/drawEvent";
import Motion from "./components/motion/motion";
import SampleCanvas from "./components/sampleCanvas/sampleCanvas";
import VideoPlay from "./components/videoPlay/videoPlay";
import CalendarComp from "./components/calendarComp/calendarComp";
import "./baseJs.less";

const modeList = [
  { order: 1, mode: LineWidth },
  { order: 2, mode: MouseEvent },
  { order: 3, mode: DrawEvent },
  { order: 4, mode: Motion },
  { order: 5, mode: SampleCanvas },
  { order: 6, mode: VideoPlay },
  { order: 7, mode: CalendarComp }
];

const BaseJs = () => {
  const [url, setUrl] = useState("");

  useEffect(() => {
    setTimeout(() => {
      setUrl("https://vjs.zencdn.net/v/oceans.webm");
    }, 300)
  })

  return (
    <div className="page-base-js">
      {
        modeList.map((item: any) => {
          return <div key={item.order} className="box">
            {
              item.order !== 6 ? <item.mode /> : <item.mode playerId="xulongji" autoplay={true} src={url || ""} className="videoJs" />
            }
          </div>;
        })
      }
    </div>
  );
};

export default BaseJs;