import React, { useEffect, useState } from 'react';
import sty from "./icomfont.module.less";

const Icomfont = () => {
  const [isShow, setIsShow] = useState(true);
  const [status, setStatus] = useState(false);

  useEffect(() => {
    console.log("xulongji", GlobalConfig.link_url);
    document.body.addEventListener("click", (e: any) => {
      const el = document.querySelector(".box");
      if (el && el.contains(e.target)) {
        setIsShow(true);
      } else {
        setIsShow(false);
      }
    }, false);

    return () => {
      document.body.removeEventListener("click", (e: any) => {
        const el = document.querySelector(".box");
        if (el && el.contains(e.target)) {
          return;
        } else {
          return;
        }
      }, false);
    }
  }, [])

  const showAction = () => {
    setIsShow(true);
  }

  const btnActon = () => {
    setStatus(!status);
  }

  return (
    <div className={sty.icomfontPage}>
      <div className={sty.left}>
        <div onClick={showAction}>字体展示页面</div>
        <div className={sty.box} style={{ height: isShow ? "2rem" : 0, width: isShow ? "2rem" : 0 }}>
          {
            [1, 2, 3, 4].map(item => {
              return <div className={sty.one} key={item}>盒子里面的类容</div>;
            })
          }
        </div>
      </div>
      <div className={sty.right}>
        <div className={sty.san} />
        <div className={sty.dian}>
          <div />
          <div />
        </div>
      </div>
      <div className={sty.box}>
        <div className={sty.traffic}>
          <div className={`${sty.light} ${sty.red}`} />
          <div className={`${sty.light} ${sty.amber}`} />
          <div className={`${sty.light} ${sty.green}`} />
        </div>
      </div>
      <div className={sty.cardBox}>
        <div className={sty.card}>
          <div className={sty.info}>
            <h2>Hover cat</h2>
            <p>Donec pede justo, fringilla vel, aliquet nec, vulputate eget, arcu. In enim justo, rhoncus ut, imperdiet a, venenatis vitae.</p>
          </div>
        </div>
      </div>
      <div className={`${sty.btnBox} ${status ? sty.on : ""}`}>
        <button className={status ? sty.active : ""} onClick={btnActon}>Show it</button>
        <div className={status ? sty.show : sty.hide}>
          <h1>Some hidden content</h1>
          <p>Pressing the button shows this content. Nice!</p>
        </div>
      </div>
      <div className={`${sty.principle} ${sty.sevenb}`}>
        <div className={`${sty.shape} ${sty.a}`}></div>
        <div className={`${sty.shape} ${sty.b}`}></div>
      </div>
      <div className={sty.justify}>
        <div className={sty.baymax}></div>
      </div>
      <div className={sty.btn}>
        <div>Oooh SHINY</div>
      </div>
      <div className={sty.move}>
        <div>abc</div>
      </div>
      <div className={sty.scroll}>
        {
          Array(50).fill("下标").map((item: any, index: number) => {
            return <div className={index === 10 ? "wow bounceInUp" : ""}>{item + index}</div>;
          })
        }
      </div>
    </div>
  );
};

export default Icomfont;