import React, { useEffect, useState } from "react";
import { Input } from "antd";
import sty from "./two.module.less";

const Two: React.FC<any> = () => {
  const [passWord, setPassWord] = useState("");

  // useEffect(() => {
  //   const draggableBox: any = document.getElementById('draggableBox');
  //   draggableBox.ondragstart = function (event: any) {
  //     console.log('开始拖拽');
  //     event.target.classList.add('draggable-style');
  //     event.dataTransfer.setData('value', '要传递的数据(只能传string类型)');
  //   }
  //   // draggableBox.ondrag = function (event: any) {
  //   //   console.log('拖拽中');
  //   // }
  //   draggableBox.ondragend = function (event: any) {
  //     console.log('拖拽结束', event);
  //     event.target.classList.add('draggable-style');
  //   }

  //   const target: any = document.getElementById('target');
  //   target.ondragenter = function (event: any) {
  //     console.log('进入目标元素');
  //     event.target.classList.add('draggable-style');
  //   }
  //   target.ondragover = function (event: any) {
  //     event.preventDefault();
  //     console.log('在目标元素中拖拽');
  //     const value = event.dataTransfer.getData('value');
  //     console.log("获取要传递的数据", value);
  //   }
  //   target.ondragleave = function (event: any) {
  //     console.log('拖放离开目标元素');
  //     event.target.classList.remove('draggable-style');
  //   }
  //   // target.ondrop = function (event: any) {
  //   //   console.log('在目标元素中拖放');
  //   // }
  // }, []);

  const changePass = (e: any) => {
    setPassWord(e.target.value);
  }

  const handleDragStart = (e: any, val: string) => {
    e.persist();
    e.target.classList.add('draggable-style');
    e.dataTransfer.setData('value', val);
  }
  
  const handleDragEnd = (e: any) => {
    e.persist();
    e.target.classList.remove('draggable-style');
  }

  const handleDragEnter = (e: any) => {
    e.preventDefault();
    e.target.classList.add('draggable-style');
    const value = e.dataTransfer.getData('value');
    console.log("value", value);
  }

  const handleDragLeave = (e: any) => {
    e.preventDefault();
    e.target.classList.remove('draggable-style');
  }

  return <div className={sty.content}>
    <div>输入框</div><Input value={passWord} onChange={changePass} />
    <div className={sty.drag}>
      <div
        draggable="true"
        onDragStart={e => handleDragStart(e, "要传递的数据(只能传string类型)")}
        onDragEnd={handleDragEnd}
      >
        拖拽的文本
      </div>
      <div
        draggable="true"
        onDragEnter={handleDragEnter}
        onDragLeave={handleDragLeave}
        // onDragOver={handleDragover}
        // onDrop={handleDrop}
      >
        目标框
      </div>
    </div>
  </div>;
};

export default Two;
