import React, { useEffect } from 'react';
import "./cssBox.less";

const CssBox = () => {

  useEffect(() => {
    let btn: any = document.querySelector('.mouse-cursor-gradient-tracking');
    btn.addEventListener('mousemove', (e: any) => {
      let rect = e.target.getBoundingClientRect();
      let x = e.clientX - rect.left;
      let y = e.clientY - rect.top;
      btn.style.setProperty('--x', x + 'px');
      btn.style.setProperty('--y', y + 'px');
    });
  }, [])

  useEffect(() => {
    const typeWriter: any = document.getElementById('typewriter-text');
    const text = 'Lorem ipsum dolor sit amet.';
    typeWriter.innerHTML = text;
    typeWriter.style.setProperty('--characters', text.length);
  }, []);

  return (
    <div className="page-base-css">
      <div className="container">Border with top triangle</div>
      <div className="bouncing-loader">
        <div></div>
        <div></div>
        <div></div>
      </div>
      <button className="animated-border-button">Submit</button>
      <button className="button-swing">Submit</button>
      <button className="animated-fill-button">Submit</button>
      <button className="button-grow">Submit</button>
      <button className="button-shrink">Submit</button>
      <div className="checkerboard"></div>
      <div className="custom-scrollbar">
        <p>
          Lorem ipsum dolor sit amet consectetur adipisicing elit.<br />
          Iure id exercitationem nulla qui repellat laborum vitae, <br />
          molestias tempora velit natus. Quas, assumenda nisi. <br />
          Quisquam enim qui iure, consequatur velit sit?
        </p>
      </div>
      <div className="donut"></div>
      <div className="dynamic-shadow"></div>
      <img className="image image-contain" src="https://picsum.photos/600/200" />
      <img className="image image-cover" src="https://picsum.photos/600/200" />
      <p className="fluid-type">Hello World!</p>
      <div className="hamburger-menu">
        <div className="bar top"></div>
        <div className="bar middle"></div>
        <div className="bar bottom"></div>
      </div>
      <div className="card-container">
        <div className="image-card perspective-left"></div>
        <div className="image-card perspective-right"></div>
      </div>
      <p className="hover-shadow-box-animation">Box it!</p>
      <p className="hover-underline-animation">Hover this text to see the effect!</p>
      <div className="hover-menu">
        <img src="https://picsum.photos/id/1060/800/480.jpg"/>
        <div>
          <a href="#">Home</a>
          <a href="#">Pricing</a>
          <a href="#">About</a>
        </div>
      </div>
      <div className="hover-rotate">
        <img src="https://picsum.photos/id/669/600/800.jpg"/>
      </div>
      <div className="hover-img">
        <img src="https://picsum.photos/id/200/440/320.jpg"/>
        <figcaption>
          <h3>Lorem <br/>Ipsum</h3>
        </figcaption>
      </div>
      <div className="isometric-card"></div>
      <div className="masonry-container">
        <div className="masonry-columns">
          <img
            className="masonry-brick"
            src="https://picsum.photos/id/1016/384/256"
            alt="An image"
          />
          <img
            className="masonry-brick"
            src="https://picsum.photos/id/1025/495/330"
            alt="Another image"
          />
          <img
            className="masonry-brick"
            src="https://picsum.photos/id/1024/192/128"
            alt="Another image"
          />
          <img
            className="masonry-brick"
            src="https://picsum.photos/id/1028/518/345"
            alt="One more image"
          />
          <img
            className="masonry-brick"
            src="https://picsum.photos/id/1035/585/390"
            alt="And another one"
          />
          <img
            className="masonry-brick"
            src="https://picsum.photos/id/1074/384/216"
            alt="Last one"
          />
        </div>
      </div>
      <button className="mouse-cursor-gradient-tracking">
        <span>Hover me</span>
      </button>
      <div className="hover-nav">
        <ul>
          <li><a href="#">Home</a></li>
          <li><a href="#">About</a></li>
          <li><a href="#">Contact</a></li>
        </ul>
      </div>
      <a className="button" href="https://google.com">
        Learn More <span className="offscreen"> about pants</span>
      </a>
      <div className="overflow-scroll-gradient">
        <div className="overflow-scroll-gradient-scroller">
          Lorem ipsum dolor sit amet consectetur adipisicing elit. <br />
          Iure id exercitationem nulla qui repellat laborum vitae, <br />
          molestias tempora velit natus. Quas, assumenda nisi. <br />
          Quisquam enim qui iure, consequatur velit sit? <br />
          Lorem ipsum dolor sit amet consectetur adipisicing elit.<br />
          Iure id exercitationem nulla qui repellat laborum vitae, <br />
          molestias tempora velit natus. Quas, assumenda nisi. <br />
          Quisquam enim qui iure, consequatur velit sit?
        </div>
      </div>
      <div className="polka-dot"></div>
      <div className="reference">
        <div className="popout-menu">Popout menu</div>
      </div>
      <div className="container">
        <p className="pretty-text-underline">Pretty text underline without clipping descenders.</p>
      </div>
      <div className="ripple-loader">
        <div></div>
        <div></div>
      </div>
      <div className="card">
        <div className="card-side front">
          <div>Front Side</div>
        </div>
        <div className="card-side back">
          <div>Back Side</div>
        </div>
      </div>
      <div className="shape-separator"></div>
      <div className="sibling-fade">
        <span>Item 1</span> <span>Item 2</span> <span>Item 3</span>
        <span>Item 4</span> <span>Item 5</span> <span>Item 6</span>
      </div>
      <div className="stripes"></div>
      <div className="overlay-box">
        <h3 className="text-overlay">Hello, World</h3>
        <img src="https://picsum.photos/id/1050/1200/800" />
      </div>
      <div className="typewriter-effect">
        <div className="text" id="typewriter-text"></div>
      </div>
      <div className="zig-zag"></div>
      <div className="zoom-in-out-box"></div>
    </div>
  );
};

export default CssBox;
